@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  src: url("/fonts/Inter-Regular.woff2") format("woff2"), url("/fonts/Inter-Regular.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: italic;
  font-weight: 400;
  src: url("/fonts/Inter-Italic.woff2") format("woff2"), url("/fonts/Inter-Italic.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  src: url("/fonts/Inter-Medium.woff2") format("woff2"), url("/fonts/Inter-Medium.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  src: url("/fonts/Inter-Bold.woff2") format("woff2"), url("/fonts/Inter-Bold.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: italic;
  font-weight: 700;
  src: url("/fonts/Inter-BoldItalic.woff2") format("woff2"), url("/fonts/Inter-BoldItalic.woff") format("woff"); }

.modalWindow {
  height: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Inter"; }

@media (min-width: 830px) {
  .modalWindow {
    height: auto; } }

.modalContentFullScreen {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto; }

@media (min-width: 830px) {
  .modalContentFullScreen {
    height: 512px; } }

.cntWithImgControl {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column; }

.title {
  line-height: 40px;
  text-align: center;
  text-transform: uppercase;
  font-size: 13px;
  border-bottom: 1px solid #f7f7f7; }

.withImgControl {
  display: none; }

.hasTransaction {
  text-align: center;
  padding: 16px 26px; }

.block {
  padding: 0 16px 16px 16px; }
  .block .labelRow {
    height: 42px;
    display: flex;
    align-items: center; }
    .block .labelRow span:nth-child(1) {
      font-size: 15px; }
    .block .labelRow span:nth-child(2) {
      color: #939ba1;
      font-size: 12px;
      margin-left: auto; }
  .block .input {
    width: 100%;
    height: 52px;
    box-sizing: border-box;
    padding: 15px;
    border: none;
    border-radius: 8px;
    margin: 0;
    background: #eef2f5; }
  .block textarea.input {
    height: 92px;
    resize: none; }
  .block .imgCnt {
    width: 140px;
    height: 140px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    border-radius: 50%;
    border: 1px dashed #939ba1;
    margin: 16px auto;
    background: #eef2f5; }
    .block .imgCnt span {
      color: #939ba1;
      font-size: 15px; }
    .block .imgCnt .imgHolder {
      width: inherit;
      height: inherit;
      position: absolute;
      left: 0;
      top: 0;
      border-radius: inherit;
      background-size: cover;
      background-position: center; }
    .block .imgCnt input.uploader {
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0;
      cursor: pointer; }
  .block .imgInfo {
    line-height: 22px;
    color: #939ba1;
    font-size: 15px; }
  .block .categorySelector {
    position: relative; }
  .block .categorySelector > div {
    width: 100%;
    top: auto;
    bottom: 0;
    margin: 0; }
  .block .categorySelector > button {
    width: 100%;
    margin: 0;
    transition-property: background;
    transition-duration: 0.4s; }
  .block .categorySelectorEmpty > button {
    background: #939ba1; }

.blockSubmit {
  display: flex;
  flex-direction: column;
  margin: 40px 0 16px 0; }
  .blockSubmit .terms {
    line-height: 22px;
    color: #939ba1;
    font-size: 15px;
    cursor: pointer; }
    .blockSubmit .terms a {
      color: #4360df;
      text-decoration: none; }
  .blockSubmit .submitButton {
    background: #4360df;
    border-radius: 8px;
    color: #fff;
    margin: 1.625rem auto 0 auto;
    border: none;
    font-family: "Inter";
    padding: 0.6875rem 2.375rem;
    font-size: 0.9375rem;
    cursor: pointer; }
  .blockSubmit .submitButton:disabled,
  .blockSubmit .submitButton[disabled] {
    background: #939ba1; }

.imgControl {
  display: flex;
  align-items: center;
  flex-direction: column;
  flex: 1 1 auto; }
  .imgControl .imgCanvasCnt {
    width: 100%;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center; }
    .imgControl .imgCanvasCnt .imgCanvas {
      width: 160px;
      height: 160px;
      border-radius: 50%;
      pointer-events: none; }
  .imgControl .controls {
    width: 100%;
    border-top: 1px solid #eef2f5; }
    .imgControl .controls .slider {
      height: 74px;
      display: flex;
      align-items: center; }
      .imgControl .controls .slider * {
        flex: 1 1 auto; }
      .imgControl .controls .slider .minZoom {
        width: 10px;
        height: 10px;
        flex: 0 0 auto;
        border: 1px solid #939ba1;
        border-radius: 3px;
        margin: auto 30px auto 15px; }
      .imgControl .controls .slider .maxZoom {
        width: 18px;
        height: 18px;
        flex: 0 0 auto;
        border: 1px solid #939ba1;
        border-radius: 3px;
        margin: auto 11px auto 26px; }
    .imgControl .controls .actionsCnt {
      height: 64px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      box-sizing: border-box;
      padding: 0 16px; }
      .imgControl .controls .actionsCnt .button {
        height: 44px;
        border-radius: 8px;
        border: none;
        border-radius: 22px;
        font-family: "Inter";
        padding: 0.6875rem 2.375rem;
        font-size: 0.9375rem;
        cursor: pointer; }
      .imgControl .controls .actionsCnt .cancelButton {
        color: #4360df;
        background: #eceffc; }
      .imgControl .controls .actionsCnt .doneButton {
        color: #fff;
        background: #4360df; }

/* rating */
.dapp {
  height: 48px;
  display: flex;
  align-items: center;
  font-family: "Inter";
  font-weight: 500;
  padding: 0 0.75rem; }

.dapp .image {
  max-width: 24px;
  max-height: 24px;
  border-radius: 50%;
  margin-right: 0.75rem; }

.items {
  display: flex;
  flex-direction: column;
  font-family: "Inter"; }

.items .itemRow {
  height: 32px;
  display: flex;
  align-items: center;
  padding: 0 0.75rem; }

.items .item {
  display: flex;
  align-items: center; }

.items .item img {
  margin-right: 0.75rem; }

.badge, .greenBadge, .redBadge {
  border-radius: 24px;
  color: #ffffff;
  font-family: "Inter";
  font-size: 0.9375rem;
  margin-right: 1rem;
  margin-left: auto;
  padding: 0.1875rem 0.625rem; }

.greenBadge {
  background: #44d058; }

.redBadge {
  background: #f00; }

.inputArea {
  width: calc(100% - 2 * 16px);
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: auto; }

.inputArea.inputAreaBorder {
  border-bottom: 1px solid #eef2f5; }

.inputArea input {
  width: 19px;
  border: none;
  text-align: center;
  font-size: 2rem;
  line-height: 1.75rem;
  font-family: "Inter";
  margin-right: 0.375rem; }

.inputArea input:focus {
  outline: none; }

.inputArea::after {
  transition: all 0.05s ease-in-out;
  content: 'SNT';
  color: #939ba1;
  font-size: 2rem;
  line-height: 1.75rem;
  font-family: "Inter"; }

.inputArea span {
  font-size: 2rem;
  line-height: 1.75rem;
  font-family: "Inter";
  margin-right: 0.375rem; }

.footer {
  text-align: center; }

.footer button {
  background: #4360df;
  border-radius: 8px;
  color: #fff;
  margin: 0.625rem auto;
  border: none;
  font-family: "Inter";
  padding: 0.6875rem 2.375rem;
  font-size: 0.9375rem;
  cursor: pointer; }

.footer button:disabled,
.footer button[disabled] {
  background: #939ba1; }

.footer .disclaimer {
  font-size: 0.9375rem;
  color: #939ba1;
  line-height: 22px;
  font-family: "Inter";
  padding: 1rem;
  border-bottom: 1px solid #eef2f5;
  margin: 0; }

.footer .disclaimer a {
  text-decoration: none;
  color: #4360df; }
